<template>
    <div id="technologies" class="app-section" :data-gradient-index="gradientIndex">
        <div class="container">
            <div class="app-section-title scrollfire-to-right">The Workshop</div>
            <div class="tech-intro flow-text scrollfire-to-top">
                <p>I consider myself to be a versatile programmer. Having experience in Javascript, Java, and SQL means that
                    I'm able to work in the 3 major areas of web development: <b>frontend</b>, <b>backend</b>, and the <b>database</b>.</p>
                <p>As the state of web development trends more towards complex frontends, 
                    so has my focus increased on staying up to date on modern frontend practices.</p>
                <p>Can you guess the technology by the logo?</p>
            </div>
            <div class="interactions scrollfire-to-top">
                <span :class="activeCategory == i ? 'active':''" 
                    class="tech-action" @click="onFilter(i)" 
                    v-for="(category,i) in categories"
                    :key="category" 
                    >{{category}}</span>
                <span class="tech-action" :class="{ active: isRevealed }" @click="toggleReveal">[ Reveal All ]</span>
            </div>
            <div class="scrollfire-to-top">
                <transition-group ref="wrapper" tag="div" name="tech" class="technology-list flow-text scrollfire-to-top">
                    <div v-for="tech in filteredTech" 
                        :class="[tech.class, { reveal: isRevealed } ]" 
                        @mouseover="tech.class = 'reveal'" 
                        @mouseout="tech.class = ''" 
                        :key="tech.name" 
                        class="technology z-depth-2-inset"  
                    >
                        <div class="img-wrapper">
                            <img :src="tech.imgSrc" :alt="tech.name" >
                        </div>
                        <div class="description-overlay">
                            <b>{{ tech.name }}</b>
                            <span v-html="tech.text"></span>
                        </div>
                    </div>
                </transition-group>
            </div>
            <persistent-dialogue :script="script"></persistent-dialogue>
            
        </div>
    </div>
</template>

<script>
import persistentDialogue from '@/components/Dialogue/DialoguePersistent'
export default {
    name:'technologies',
    data () {
        return {
            gradientIndex: 1,
            isRevealed: false,
            technologies: this.$store.state.technologies,
            categories: this.$store.state.technologyCategories,
            activeCategory: 0,
            script:[
                {speaker:'robot', lines:["Sire is the best developer at guanzo manor"]},
                {speaker:'man', lines:["That is technically true","Is the gallery ready?" ]},
                {speaker:'robot', lines:["Y-yes SIRE!"]},
            ],
        }
    },
    computed:{
        filteredTech(){
            if(this.activeCategory == 0)
                return this.technologies
            else
                return this.technologies.filter(d=>d.category.includes(this.activeCategory))
        },
    },
    watch:{
        filteredTech(){
            this.animateContainerHeight();
        }
    },
    methods:{
        onFilter(index){
            this.activeCategory = parseInt(index);
        },
        toggleReveal(){
            this.isRevealed = !this.isRevealed;
        },
        animateContainerHeight(){
            let el = this.$refs.wrapper.$el
            let beforeHeight = el.clientHeight
            this.$nextTick(()=>{
                let afterHeight = el.clientHeight
                el.style.height = beforeHeight+'px'
                el.offsetHeight
                el.style.height = afterHeight+'px'
                setTimeout(()=>el.style.height = 'auto',1000)
            })
        }
    },
    components:{
        persistentDialogue,
    }
}

</script>


<style lang="scss" scoped>



$tech-size: 125px;
$small-tech-size: 85px;

.guess{
    position: relative;
    display:flex;
    justify-content: space-between;
}

.z-depth-2-inset{
    box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14) inset, 
                0 1px 10px 0 rgba(0,0,0,0.12) inset, 
                0 2px 4px -1px rgba(0,0,0,0.3) inset;
}

.interactions{
    padding: 15px 0px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    span{
        margin:0px 0.25em;
        padding:0px 0.25em;
        cursor: pointer;
    }
    span.line{
        cursor: default;
    }
    .tech-action{
        transition: .75s;
        border-radius:4px;
        &.active{
            background: $offwhite;
            color:#333;
        }
        &:not(.active):hover{
            background: rgba($offwhite,0.2);
        }
    }
}

.technology-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    font-size: 0.8rem;
    color: #333;
    transition: height .75s;// to animate height change due to filtering content
}

.technology{
    transition: .75s;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    border-radius: 50%;
    width: $small-tech-size;
    height: $small-tech-size;
    padding: 5px;
    margin: 5px;
    &.reveal div.description-overlay{
        opacity: 1;
    }
    &.reveal img{
        filter: blur(2px);
    }   
}
@media (min-width: 400px){
    .technology-list {
        font-size: 0.85rem;
        .technology{
            width: $tech-size;
            height: $tech-size;
        }
    }
}
div.description-overlay{
    position: absolute;
    opacity: 0;
    height:100%;
    width:100%;
    background-color:rgba(255,255,255,0.75);
    transition: 0.75s;
    padding: 5px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.img-wrapper{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
img{
    max-width:100%;
    max-height:100%;
    transition: 0.75s;
    will-change: filter;
}

.tech-leave-active{
    transition: all .75s, opacity .5s;
    position: absolute;
}

.tech-enter, .tech-leave-to{
    opacity: 0;
}

.tech-enter{
    transform: translateY(30px);
}


</style>



